function binarySearch(arr, val){
    let minIdx = 0,
        maxIdx = arr.length-1,
        guessIdx;
    while(minIdx<maxIdx){
        guessIdx = Math.floor((maxIdx+minIdx)/2)
        if(arr[guessIdx] === val){
            return guessIdx
        }else if(arr[minIdx]<arr[guessIdx]){
            minIdx = minIdx + 1
        }else{
            maxIdx = maxIdx - 1
        }
    }
    return -1
}

